import { NextRequest, NextResponse } from 'next/server';
import GoogleSheetsService from '@/lib/sheets/service';
import { GeminiParser } from '@/lib/ai/gemini-parser';
import { BetData } from '@/lib/telegram/parser';

const sheetsConfig = {
  spreadsheetId: process.env.GOOGLE_SHEETS_ID!,
  range: 'Apostas!A:J',  // Mudan√ßa de A:L para A:J
  credentials: {
    client_email: process.env.GOOGLE_CLIENT_EMAIL!,
    private_key: (process.env.GOOGLE_PRIVATE_KEY || '').replace(/\\n/g, '\n'),
  },
};

const sheetsService = new GoogleSheetsService(sheetsConfig);

// Cache tempor√°rio para aguardar respostas dos usu√°rios
const pendingConfirmations = new Map<string, any>();

// NOVO: M√©todo GET para teste
export async function GET() {
  return NextResponse.json({
    status: 'Webhook ativo! ü§ñ',
    timestamp: new Date().toISOString(),
    message: 'Este endpoint est√° funcionando corretamente para receber webhooks do Telegram.'
  });
}

export async function POST(request: NextRequest) {
  try {
    const update = await request.json();
    console.log('Update recebido:', JSON.stringify(update, null, 2));
    
    if (update.message && update.message.text) {
      const chatId = update.message.chat.id;
      const userId = update.message.from.id;
      const username = update.message.from.username;
      const messageText = update.message.text;
      const messageLower = messageText.toLowerCase().trim();
      
      console.log(`Mensagem de ${username} (${userId}): "${messageText}"`);
      
      const pendingKey = `${chatId}_${userId}`;
      const oddKey = `${pendingKey}_odd`;
      
      // 1. Verificar se est√° aguardando odd real
      if (pendingConfirmations.has(oddKey)) {
        console.log('Processando odd real...');
        await handleOddResponse(update, oddKey);
        return NextResponse.json({ ok: true });
      }
      
      // 2. Verificar se √© resposta SIM/N√ÉO para confirma√ß√£o de aposta
      if (pendingConfirmations.has(pendingKey) && 
          (messageLower === 'sim' || messageLower === 'n√£o' || messageLower === 'nao')) {
        console.log('Processando confirma√ß√£o de aposta...');
        await handleBetConfirmation(update, pendingKey);
        return NextResponse.json({ ok: true });
      }
      
      // 3. Processar nova mensagem de aposta com IA (MUDAN√áA AQUI!)
      console.log('ü§ñ Analisando mensagem com Gemini AI...');
      const betData = await GeminiParser.parseBetMessage(
        messageText, 
        chatId, 
        userId, 
        username
      );
      
      if (betData) {
        console.log('‚úÖ Nova aposta detectada pela IA:', betData);
        
        // Salvar temporariamente e solicitar confirma√ß√£o
        pendingConfirmations.set(pendingKey, betData);
        
        // Enviar mensagem de confirma√ß√£o
        await sendTelegramMessage(
          chatId, 
          GeminiParser.createConfirmationMessage(betData)
        );
        
        console.log('üì§ Mensagem de confirma√ß√£o enviada');
      } else {
        console.log('‚ùå Mensagem n√£o reconhecida como aposta v√°lida pela IA');
      }
    }
    
    return NextResponse.json({ ok: true });
  } catch (error) {
    console.error('Erro no webhook do Telegram:', error);
    return NextResponse.json({ ok: false }, { status: 500 });
  }
}

async function handleBetConfirmation(update: any, pendingKey: string) {
  const chatId = update.message.chat.id;
  const userId = update.message.from.id;
  const messageLower = update.message.text.toLowerCase().trim();
  
  const betData = pendingConfirmations.get(pendingKey);
  if (!betData) {
    console.log('Dados da aposta n√£o encontrados no cache');
    return;
  }
  
  console.log(`Confirma√ß√£o recebida: ${messageLower}`);
  
  if (messageLower === 'sim') {
    betData.pegou = true;
    
    // Mover para aguardar odd real
    const oddKey = `${pendingKey}_odd`;
    pendingConfirmations.set(oddKey, betData);
    pendingConfirmations.delete(pendingKey);
    
    // Perguntar a odd real
    await sendTelegramMessage(
      chatId,
      `‚úÖ **Confirmado!**\n\nüí∞ **Qual foi a odd real que voc√™ conseguiu?**\n\nDigite apenas o n√∫mero (ex: 1.85)`
    );
    
    console.log('Pergunta sobre odd real enviada');
  } else {
    betData.pegou = false;
    betData.odd_real = null;
    
    // Salvar no Google Sheets
    const success = await sheetsService.addBetData(betData);
    
    await sendTelegramMessage(
      chatId,
      `‚ùå **Aposta n√£o realizada**\n\nDados salvos no sistema.`
    );
    
    pendingConfirmations.delete(pendingKey);
    console.log(`Aposta n√£o realizada salva. Sucesso: ${success}`);
  }
}

async function handleOddResponse(update: any, oddKey: string) {
  const chatId = update.message.chat.id;
  const messageText = update.message.text;
  
  const betData = pendingConfirmations.get(oddKey);
  if (!betData) {
    console.log('‚ùå Dados da aposta n√£o encontrados para odd real');
    return;
  }
  
  const oddReal = parseFloat(messageText.replace(',', '.'));
  console.log(`üìä Odd real recebida: ${messageText} -> ${oddReal}`);
  
  if (!isNaN(oddReal) && oddReal > 0) {
    betData.odd_real = oddReal;
    
    console.log('üíæ Tentando salvar no Google Sheets:', betData);
    
    // Testar conex√£o primeiro
    const connectionOk = await sheetsService.testConnection();
    if (!connectionOk) {
      console.error('‚ùå Falha na conex√£o com Google Sheets');
      await sendTelegramMessage(chatId, '‚ùå **Erro de conex√£o**\n\nN√£o foi poss√≠vel conectar ao Google Sheets.');
      return;
    }
    
    // Salvar no Google Sheets
    const success = await sheetsService.addBetData(betData);
    
    if (success) {
      await sendTelegramMessage(
        chatId,
        `‚úÖ **Aposta registrada com sucesso!**\n\n` +
        `‚öΩ **Jogo:** ${betData.jogo}\n` +
        `üìä **Mercado:** ${betData.mercado}\n` +
        `üìà **Linha:** ${betData.linha_da_aposta}\n` +
        `üí∞ **Odd Tipster:** ${betData.odd_tipster}\n` +
        `üíé **Odd Real:** ${betData.odd_real}\n` +
        `üìä **Status:** ${betData.resultado_aposta}`
      );
      
      console.log('‚úÖ Aposta salva com sucesso no Google Sheets');
    } else {
      await sendTelegramMessage(
        chatId,
        `‚ùå **Erro ao salvar**\n\nA aposta foi processada mas houve erro ao salvar no Google Sheets. Verifique os logs.`
      );
      
      console.error('‚ùå Falha ao salvar no Google Sheets');
    }
    
    pendingConfirmations.delete(oddKey);
  } else {
    await sendTelegramMessage(
      chatId,
      `‚ùå **Odd inv√°lida**\n\nPor favor, digite um n√∫mero v√°lido (ex: 1.85)`
    );
    console.log('‚ùå Odd inv√°lida, aguardando nova resposta');
  }
}

async function sendTelegramMessage(chatId: number, text: string) {
  const botToken = process.env.TELEGRAM_BOT_TOKEN!;
  
  try {
    console.log(`Enviando mensagem para chat ${chatId}:`, text);
    
    const response = await fetch(`https://api.telegram.org/bot${botToken}/sendMessage`, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({
        chat_id: chatId,
        text: text,
        parse_mode: 'Markdown'
      })
    });
    
    const result = await response.json();
    
    if (!response.ok) {
      console.error('Erro na API do Telegram:', result);
    } else {
      console.log('Mensagem enviada com sucesso');
    }
    
    return result;
  } catch (error) {
    console.error('Erro ao enviar mensagem:', error);
  }
}